
import Panel from 'vux/src/components/panel/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import PullTo from 'vue-pull-to'

export default {
components: {
PullTo, Panel, LoadMore
},
data () {
return {
type: '1',
list: [],
infiniteLoading: true,
infiniteTip: "正在加载",
iconLink: '', //下拉刷新的状态
}
},
mounted() {
this.ysData();
},
methods: {
onImgError (item, $event) {

},

infinite(done){
if (this.bottom >= 120) {
this.infiniteLoading = false;
this.infiniteTip = "暂无更多";
return false;
}
let start = this.bottom + 1
for (let i = start; i < start + 10; i++) {
this.ajaData(i);
}
this.bottom = this.bottom + 10;
setTimeout(() => {


}, 600)
},

ysData(){
this.list=[];
for (let i = 1; i <= 10; i++) {
this.ajaData(i);
}
this.bottom = 10
},

ajaData(i){
var obj = {
src: 'https://o5omsejde.qnssl.com/demo/test2.jpg',
fallbackSrc: 'http://placeholder.qiniudn.com/60x60/3cc51f/ffffff',
title: '标题'+i,
desc: '由各种物质组成的巨型球状天体，叫做星球。星球有一定的形状，有自己的运行轨道。',
url: '/MessageHome/details?id='+i
};
this.list.push(obj);
},

refresh(loaded) {
setTimeout(() => {
this.infiniteLoading = true;
this.infiniteTip = "正在加载";
this.ysData();
loaded('done');
}, 600);
},

stateChange(state) {
if (state === 'pull' || state === 'trigger') {
this.iconLink = '#icon-arrow-bottom';
} else if (state === 'loading') {
this.iconLink = '#icon-loading';
} else if (state === 'loaded-done') {
this.iconLink = '#icon-finish';
}
}
}
}



